}
.response-info_reviews{
	margin-bottom: 89px;
}
.response-company__edit{
	margin-top: 10px;
	display: inline-block;
	border-bottom: 1px solid;
	font-size: 18px;
	line-height: 18px;
}
@media screen and (max-width: 1024px) {
	.box-sort__title{
		text-align: left;
	}
	.box-sort{
		margin-bottom: 20px;
	}
	.box-sort__item:first-child{
		width: auto;
		padding-right: 30px;
	}
}
@media screen and (max-width: 767px){
	.box-sort__item:first-child{
		display: block;
		margin-right: 0;
	}
	.box-sort__title{
		top: 0;
	}
	.inner-block__main_reviews{
		padding-top: 0;
	}
	.box-sort__item{
		display: block;
		width: 100%;
	}
	.filter__drop{
		width: 100%;
	}
	.box-sort__item_rating{
		margin-left: 0;
	}
	.box-sort__item{
		margin-top: 10px;
	}
}